"use client";

import * as Plot from "@observablehq/plot";
import { useEffect, useRef } from "react";
import { partyColors } from "@/lib/config/colors";

interface TrendData {
  date: string;
  party: string;
  metric: string;
  value: number;
}

interface PollingChartProps {
  data: TrendData[];
  height?: number;
}

export function PollingChart({ data, height = 300 }: PollingChartProps) {
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!data || data.length === 0) return;

    // Filter for vote share data only
    const voteShareData = data.filter(d => d.metric === 'vote_share_mean');
    
    const plot = Plot.plot({
      height,
      marginLeft: 60,
      marginRight: 80,
      marginTop: 20,
      marginBottom: 40,
      style: {
        backgroundColor: "transparent",
        fontSize: "12px",
        fontFamily: "Inter, system-ui, sans-serif"
      },
      x: {
        type: "time",
        label: null,
        tickFormat: "%b %Y",
        grid: true,
        ticks: 4
      },
      y: {
        label: "Vote share (%)",
        domain: [0, 0.5],
        tickFormat: d => `${(d * 100).toFixed(0)}%`,
        grid: true
      },
      color: {
        type: "categorical",
        domain: Object.keys(partyColors),
        range: Object.values(partyColors)
      },
      marks: [
        // Background grid
        Plot.gridY({ stroke: "#f3f4f6", strokeWidth: 1 }),
        Plot.gridX({ stroke: "#f3f4f6", strokeWidth: 1 }),
        
        // Lines for each party
        Plot.line(voteShareData, {
          x: d => new Date(d.date),
          y: "value",
          stroke: "party",
          strokeWidth: 2.5,
          curve: "catmull-rom"
        }),
        
        // Points for latest values
        Plot.dot(voteShareData.filter(d => {
          const maxDate = new Date(Math.max(...voteShareData.map(dd => new Date(dd.date).getTime())));
          return new Date(d.date).getTime() === maxDate.getTime();
        }), {
          x: d => new Date(d.date),
          y: "value",
          fill: "party",
          r: 4,
          stroke: "white",
          strokeWidth: 2
        }),
        
        // Party labels at the end
        Plot.text(voteShareData.filter(d => {
          const maxDate = new Date(Math.max(...voteShareData.map(dd => new Date(dd.date).getTime())));
          return new Date(d.date).getTime() === maxDate.getTime();
        }), {
          x: d => new Date(d.date),
          y: "value",
          text: d => `${d.party} ${(d.value * 100).toFixed(1)}%`,
          fill: "party",
          dx: 10,
          fontSize: 11,
          fontWeight: "500"
        })
      ]
    });

    if (containerRef.current) {
      containerRef.current.replaceChildren(plot);
    }

    return () => plot.remove();
  }, [data, height]);

  return (
    <div className="w-full">
      <div ref={containerRef} />
    </div>
  );
}